<template>
  <div v-for="items in menu" :key="items.id" class="w-full">
    <div v-for="(value,index) in items" :key="value">
      <div class="flex flex-row align-center justify-between m-3 w-full">
        <div class=" w-9/12 flex flex-row my-2">
          <span class="text-black font-thin">{{index+=1}}</span>
          <img :src="`${value.player.image}`" @error="notShow"  class="w-10 h-10 rounded-full mx-2" alt="">

          <div class="flex flex-col space-y-1">
            <div><p class="text-black font-thin">{{value.player.name_fa}}</p></div>
            <div class="flex flex-row">
              <img :src="`${value.team.logo}`" @error="notShow" alt="" class="w-5 h-5 rounded-full"/>
              <p class="text-black font-thin">{{value.team.name_fa}}</p>
            </div>
          </div>
        </div>
        <div class=" w-3/12 flex flex-row align-center justify-around">
          <div><span class="font-thin text-black text-start">{{value.penalty}}</span></div>
          <div><span class="font-thin text-black text-start">{{value.goal}}</span></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "NumberGoals",
  props:{
    menu:{
      type:Array,
      required:true,
      default:null
    }
  },
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_player_default.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      notFound,notShow,
    }
  }
}
</script>

